module.exports = ".transition, :host {\n  transition: all 200ms cubic-bezier(0.7, 0, 0.3, 1); }\n\n.transition-slow {\n  transition: all 520ms cubic-bezier(0.7, 0, 0.3, 1); }\n\n/* CLEARFIX */\n\n.clearfix {\n  *zoom: 1; }\n\n.clearfix:before, .clearfix:after {\n    content: \" \";\n    display: table; }\n\n.clearfix:after {\n    clear: both; }\n\n.center-element {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  -webkit-transform: translateX(-50%) translateY(-50%);\n  transform: translateX(-50%) translateY(-50%); }\n\n.center-left {\n  position: absolute;\n  left: 50%;\n  -webkit-transform: translateX(-50%);\n  transform: translateX(-50%); }\n\n.center-top {\n  position: absolute;\n  top: 50%;\n  -webkit-transform: translateY(-50%);\n  transform: translateY(-50%); }\n\n.box-sizing, :host {\n  box-sizing: border-box; }\n\n:host {\n  -webkit-transform: translateX(-100%);\n  transform: translateX(-100%);\n  padding: 90px 30px 40px 45px;\n  width: 300px;\n  background: #f9f9f9;\n  box-shadow: inset -35px -18px 70px rgba(233, 233, 233, 0.25);\n  position: fixed !important;\n  border-right: 1px solid #efefef;\n  bottom: 0;\n  top: 0;\n  z-index: 1000;\n  overflow: hidden; }\n\n:host.opened {\n    -webkit-transform: translateX(0);\n    transform: translateX(0); }\n\n@media only screen and (max-width: 1200px) {\n    :host {\n      width: 300px; } }\n\n@media only screen and (max-width: 991px) {\n    :host.opened {\n      z-index: 1000;\n      box-shadow: 0 10px 40px rgba(0, 0, 0, 0.1); } }\n\n@media only screen and (max-width: 767px) {\n    :host {\n      padding: 20px 25px 20px 25px; } }\n\n@media only screen and (max-width: 480px) {\n    :host {\n      width: 100%; } }\n\n.nav-item {\n  margin: 0 -10px;\n  padding: 0 10px; }\n\n.nav-item:last-of-type {\n    border: 0; }\n\n.nav-item h3 {\n    margin: 20px 0; }\n"