module.exports = ".transition, :host, footer a, .social-wrapper a {\n  transition: all 200ms cubic-bezier(0.7, 0, 0.3, 1); }\n\n.transition-slow {\n  transition: all 520ms cubic-bezier(0.7, 0, 0.3, 1); }\n\n/* CLEARFIX */\n\n.clearfix {\n  *zoom: 1; }\n\n.clearfix:before, .clearfix:after {\n    content: \" \";\n    display: table; }\n\n.clearfix:after {\n    clear: both; }\n\n.center-element {\n  position: absolute;\n  top: 50%;\n  left: 50%;\n  -webkit-transform: translateX(-50%) translateY(-50%);\n  transform: translateX(-50%) translateY(-50%); }\n\n.center-left {\n  position: absolute;\n  left: 50%;\n  -webkit-transform: translateX(-50%);\n  transform: translateX(-50%); }\n\n.center-top, .social-wrapper {\n  position: absolute;\n  top: 50%;\n  -webkit-transform: translateY(-50%);\n  transform: translateY(-50%); }\n\n.box-sizing, :host {\n  box-sizing: border-box; }\n\n:host {\n  background: #191919;\n  display: block;\n  height: 90px;\n  padding: 5px 2px 15px 50px;\n  position: absolute;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  margin: 0;\n  width: auto;\n  font-size: 15px; }\n\n@media only screen and (max-width: 767px) {\n    :host {\n      padding: 15px;\n      margin: 0 -30px;\n      width: calc(100% + 60px);\n      position: static;\n      height: auto;\n      text-align: center; } }\n\nfooter {\n  display: block;\n  position: relative;\n  color: #929292; }\n\nfooter a {\n    color: #929292; }\n\nfooter a:hover {\n      color: #f9f8f8; }\n\nfooter p {\n    line-height: 25px; }\n\n.social-wrapper {\n  position: absolute;\n  right: 40px; }\n\n@media only screen and (max-width: 767px) {\n    .social-wrapper {\n      -webkit-transform: translateX(0) translateY(0);\n      transform: translateX(0) translateY(0);\n      position: static;\n      text-align: center; } }\n\n.social-wrapper a {\n    display: inline-block;\n    vertical-align: middle;\n    color: #a5a5a5;\n    font-size: 22px;\n    margin-left: 12px;\n    cursor: pointer; }\n\n.social-wrapper a:first-of